import React from "react";
import { FormInputEmail } from "../../components/from-container/inputs/email";
import { FormInputName } from "../../components/from-container/inputs/name";
import { FormInputPassword } from "../../components/from-container/inputs/password";
import { Link } from "react-router-dom";
import { FormFooterLinks } from "../../components/from-container/links/links";
import styles from "../../components/from-container/form-container.module.css";
import { Button } from "@ya.praktikum/react-developer-burger-ui-components";
import { FormInputToken } from "../../components/from-container/inputs/token";

export const Inputs = {
    email: <FormInputEmail placeholder="E-mail" key="email" name="email" />,
    password: <FormInputPassword placeholder="" key="password" name="password" />,
    newPassword: <FormInputPassword placeholder="  " key="newPassword" name="newPassword" />,
    token: <FormInputToken placeholder="    " key="token" name="token" />,
    name: <FormInputName placeholder="" key="name" name="name" />,
    specifyEmail: <FormInputEmail placeholder=" E-mail" key="specifyEmail" name="specifyEmail" />,
    profileName: <FormInputName placeholder="" key='name' name='name' disabled={true} />,
    profileEmail: <FormInputEmail placeholder="E-mail" key='email' name='email' />,
    profilePassword: <FormInputPassword placeholder="" key='password' name='password' />
}

export const Links = {
    alreadyRegistered: <FormFooterLinks key="alreadyRegistered" infoText=" ?"><Link to="/login" className={`${styles.link}`}></Link></FormFooterLinks>,
    rememberPassword: <FormFooterLinks key="rememberPassword" infoText=" ?"><Link to="/login" className={`${styles.link}`}></Link></FormFooterLinks>,
    forgotPassword: <FormFooterLinks key="forgotPassword" infoText=" ?"><Link to="/forgot-password" className={`${styles.link}`}> </Link></FormFooterLinks>,
    newUser: <FormFooterLinks key="newUser" infoText=" -  ?"><Link to="/register" className={`${styles.link}`}></Link></FormFooterLinks>
}

export const navigateButton = ({ onClick, label }) => {
    return <Button htmlType="submit" type="primary" size="medium" children={label} onClick={onClick} />
}

export const Buttons = {
    save: <Button htmlType="submit" type="primary" size="medium" children="" key='save' />,
    cancel: <Button htmlType="reset" type="secondary" size="medium" children="" key='cancel' />
}